/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect, useRef } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.scss";
import "rodal/lib/rodal.css";
import "./index.scss";
import { useParams } from "react-router-dom";
import Slider from "react-slick";
import { FullScreen, useFullScreenHandle } from "react-full-screen";
import Rodal from "rodal";
import { useDispatch, useSelector } from "react-redux";
import ReactHtmlParser from "react-html-parser";
import moment from "moment";

// actions
import { getDetailProject } from "../../../stores/reducers/projectSlice";
// icons
import { CIcon } from "@coreui/icons-react";

// ui
import { CRow, CCol } from "@coreui/react";

// components
import Header from "../Layouts/NewHeader";
import Footer from "../Layouts/NewFooter";

const ProjectDetail = (props) => {
  const { id } = useParams();
  const sliderRef = useRef(null);
  const contentRef = useRef(null);
  const dispatch = useDispatch();
  const { projectDetail } = useSelector((state) => state.projects);

  const [isFixed, setIsFixed] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(1);
  const [imageViewFull, setImageViewFull] = useState(null);
  const [openGrid, setOpenGrid] = useState(false);

  const handle = useFullScreenHandle();

  const settings = {
    dots: true,
    fade: true,
    infinite: true,
    speed: 500,
    // autoplay: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    beforeChange: (current, next) => {
      console.log(current, next);
      setCurrentIndex(next + 1);
    },
  };

  const handleScroll = (event) => {
    if (window.pageYOffset > 5) {
      setIsFixed(true);
    } else {
      setIsFixed(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    dispatch(getDetailProject(id));
  }, [dispatch, id]);

  const handleBackTop = () => {
    window.scrollTo(0, 0);
  };

  const handleViewFullScreen = () => {
    if (projectDetail && projectDetail.gallery.length > 0) {
      const item = projectDetail.gallery[currentIndex - 1];
      const srcData = `${process.env.REACT_APP_CLOUD_FRONT_URI}${
        item && item.key
      }`;

      setImageViewFull(srcData);
      handle.enter();
    }
  };

  const handleToggleGirds = () => {
    setOpenGrid(!openGrid);
  };

  const handleSelectImage = (index) => {
    setCurrentIndex(index);
    sliderRef.current.slickGoTo(index);
    setOpenGrid(false);
  };

  const handleViewContent = () => {
    contentRef.current.scrollIntoView();
  };

  const handleChangeFullScreen = (state, handle) => {
    if (!state) {
      setImageViewFull(null);
    }
  };

  return (
    <div className="projectDetail">
      <Header />
      <div className="projectDetailContent">
        <div className="banner">
          <Slider ref={sliderRef} className="silderBanner" {...settings}>
            {projectDetail &&
              projectDetail.gallery &&
              projectDetail.gallery.length > 0 &&
              projectDetail.gallery.map((item, i) => (
                <div className="silderItem" key={i}>
                  <img
                    className="sliderImage"
                    src={`${process.env.REACT_APP_CLOUD_FRONT_URI}${
                      item && item.key
                    }`}
                    alt=""
                  />
                </div>
              ))}
          </Slider>

          <div className="toolBar">
            <div className="toolLeft">
              <h3 className="showIndexSlider">
                {currentIndex}/
                {(projectDetail &&
                  projectDetail.gallery &&
                  projectDetail.gallery.length) ||
                  1}
              </h3>
            </div>
            <div className="toolRight">
              <a onClick={handleViewContent}>
                <CIcon name="cil-list" />
              </a>
              <a onClick={handleToggleGirds}>
                <CIcon name="cil-grid" />
              </a>
              <a onClick={handleViewFullScreen}>
                <CIcon name="cil-fullscreen" />
              </a>
            </div>
          </div>
        </div>

        <div className="content" ref={contentRef}>
          <div className="descriptionCity ql-editor">
            {ReactHtmlParser(
              (projectDetail &&
                projectDetail.cityProject &&
                projectDetail.cityProject.description) ||
                ""
            )}
          </div>

          <div className="listDetail">
            <div className="lineItem">
              <h3>Project name</h3>
              <p>{(projectDetail && projectDetail.name) || ""}</p>
            </div>

            <div className="lineItem">
              <h3>Location</h3>
              <p>
                {(projectDetail &&
                  projectDetail.cityProject &&
                  projectDetail.cityProject.city) ||
                  ""}
                {", "}
                {(projectDetail &&
                  projectDetail.cityProject &&
                  projectDetail.cityProject.country) ||
                  ""}
              </p>
            </div>

            <div className="lineItem">
              <h3>Investment Date</h3>
              <p>
                {(projectDetail &&
                  projectDetail.investmentDate &&
                  moment(projectDetail.investmentDate).format("MM/DD/YYYY")) ||
                  ""}
              </p>
            </div>

            <div className="lineItem">
              <h3>Year</h3>
              <p>
                {(projectDetail &&
                  projectDetail.investmentDate &&
                  moment(projectDetail.investmentDate).year()) ||
                  ""}
              </p>
            </div>

            <div className="lineItem">
              <h3>Status</h3>
              <p>{(projectDetail && projectDetail.status) || ""}</p>
            </div>

            <div className="lineItem">
              <h3>Client</h3>
              <p>
                {(projectDetail &&
                  projectDetail.userAssigned &&
                  projectDetail.userAssigned.firstName) ||
                  ""}{" "}
                {(projectDetail &&
                  projectDetail.userAssigned &&
                  projectDetail.userAssigned.lastName) ||
                  ""}
              </p>
            </div>
          </div>
        </div>
      </div>

      {isFixed && (
        <div className="backTop" onClick={handleBackTop}>
          <CIcon name="cilArrowTop" />
        </div>
      )}

      <Rodal
        visible={openGrid}
        onClose={handleToggleGirds}
        width="100%"
        height="100vh"
      >
        <div className="viewGalleryImages">
          <CRow>
            {projectDetail &&
              projectDetail.gallery &&
              projectDetail.gallery.length > 0 &&
              projectDetail.gallery.map((item, i) => (
                <CCol key={i} xs={12} sm={6} md={4}>
                  <div
                    className="galleryItem"
                    onClick={() => handleSelectImage(i)}
                  >
                    <img
                      src={`${process.env.REACT_APP_CLOUD_FRONT_URI}${
                        item && item.key
                      }`}
                      alt=""
                    />
                  </div>
                </CCol>
              ))}
          </CRow>
        </div>
      </Rodal>

      {/* {imageViewFull && ( */}
      <FullScreen handle={handle} onChange={handleChangeFullScreen}>
        {imageViewFull && (
          <img
            alt=""
            src={imageViewFull}
            style={{ width: "100%", height: "100vh" }}
          />
        )}
      </FullScreen>
      {/* )} */}

      <Footer />
    </div>
  );
};

export default ProjectDetail;
